// Завантажуємо reviews.json
fetch('path/to/reviews.json')
  .then(response => response.json())
  .then(data => {
    const reviewsList = document.getElementById('reviews-list');
    
    // Перебираємо масив відгуків
    data.reviews.forEach(review => {
      const reviewElement = document.createElement('div');
      reviewElement.classList.add('review-item');
      
      // Додаємо HTML вміст для кожного відгуку
      reviewElement.innerHTML = `
        <div class="review-header">
          <h3>${review.user}</h3>
          <p class="review-date">${review.date}</p>
        </div>
        <div class="review-rating">
          ${'★'.repeat(review.rating)} ${'☆'.repeat(5 - review.rating)} <!-- Створюємо зірки за допомогою повторення -->
        </div>
        <p class="review-text">${review.text}</p>
      `;
      
      // Додаємо кожен відгук до контейнера
      reviewsList.appendChild(reviewElement);
    });
  })
  .catch(error => {
    console.error('Помилка при завантаженні відгуків:', error);
  });

